<template>
  <div id="search">
    <div id="status-bar-placeholder"></div>
  <!-- error cover screen -->
    <error-view v-show="showError"></error-view>

    <!-- loading cover -->
    <loading-cover v-show="isLoading" transition="fade">
    </loading-cover>

    <mt-search :value.sync="query" cancel-text="取消" placeholder="请输入商品关键字" :result.sync="suggestions">
      <mt-cell
        v-show="showMtCell"
        v-for="sug in suggestions" 
        track-by="$index"
        @click="didTap(sug)"

        :title="sug">
      </mt-cell>
    </mt-search>

    <search-results-list :results="results" v-show="showSearchResultsList"></search-results-list>

  </div>
</template>

<script>
import LoadingCover from './shared/LoadingCover.vue'
import ErrorView from './shared/ErrorView.vue'
import SearchResultsList from './search/SearchResultsList.vue'

export default {
  name: 'SearchView',

  components: {
    ErrorView,
    LoadingCover,
    SearchResultsList,
  },

  data() {
    return {
      query: '',
      suggestions: [],
      results: [],
      showError: false,
      isLoading: false,
      showSearchResultsList: false,
      showMtCell: false
    }
  }, 
  ready() {
    this.bindCancel()
  }, 

  watch: {
    'query': function(val, oldVal) {
      console.log("query: " + val)
      if (val.length > 0) {
        this.getAutocomplete()
      } 
    }
  },

  methods: {

    /* 绑定取消按钮 */
    bindCancel() {

      var c = document.getElementsByClassName('mint-searchbar-cancel')[0]
      //激活 cancel button
      c.addEventListener('click', didCancel, false)
    },

    didTap(sug) {
      var s = sug.split(' ').join('+')
      s = s.replace(/[（）()\/\\]/g, '')
      console.log("[SearchView didTap] suggestion: " + s)
      // window.location.href = "#!/search/" + s

      // get result items
      this.getResults(s) 
    },

    getAutocomplete () {
      var vm = this;

      // request callback
      var callback = function(response) {
          console.log("[getQuery callback] response: " + JSON.stringify(response.body));
          vm.$set('suggestions', response.json());
          vm.$set('showMtCell', true)
          vm.$set('showSearchResultsList', false)
      }

      var errorCallback = function(response) {
          console.log("[error callback] response: " + JSON.stringify(response));
          // show error
          vm.$set('showError', true)
      }

      // request url 
      var url = API_URL + "/api/items/autocomplete";

      // search param
      var query = this.query
      var params = {query: query}

      // request headers
      var headers = {
          "Content-Type": "application/json",
          "Accept": "application/json",
          "X-APP-KEY": APP_KEY,
          "X-APP-SECRET": APP_SECRET
      };

      // vue-resource
      this.$http.get(url, {headers: headers, params: params}).then(callback, errorCallback);
    }, 

    getResults (query) {
      var vm = this;

      // check query is valid
      if (query.length < 1) { return }
        
      // request callback
      var callback = function(response) {
          console.log("[getResults callback] response: " + JSON.stringify(response.body));
          vm.$set('results', response.json());

          // remove loading cover
          vm.$set('isLoading', false)

          vm.$set('showMtCell', false)
          vm.$set('showSearchResultsList', true)
      }

      var errorCallback = function(response) {
          console.log("[error callback] response: " + JSON.stringify(response));

          // remove loading cover
          vm.$set('isLoading', false)

          // show error
          vm.$set('showError', true)
      }

      // request url 
      var url = API_URL + "/api/items/";
      var params = {query: query}

      // request headers
      var headers = {
          "Content-Type": "application/json",
          "Accept": "application/json",
          "X-APP-KEY": APP_KEY,
          "X-APP-SECRET": APP_SECRET
      };

      // vue-resource
      this.$http.get(url, {headers: headers, params: params}).then(callback, errorCallback);
    },
  }
}
</script>

<style>

  /* set height to initial to allow results to render below */
  #search .mint-search {
    height: initial;
  }

  /* white background color */
  #search .mint-searchbar {
    background-color: #fff; 
  }

  /* account for ios status bar */
  #status-bar-placeholder {
    height: 20px;
    background-color: #fff;
  }

  /* account for ios status bar */
  #search .mint-search-list {
    padding-top: 64px;
  }

  /* font size */
  #search .mint-searchbar-placeholder,
  #search .mint-searchbar-core,
  #search .mintui.mintui-search {
    font-size: 15px;
  }

  /* search input style */
  #search .mint-searchbar-core {
    padding: 0 0.6em;
    border-radius: 6px;
    background-color: #e5e5e5;
  } 

  /* searchbar icon */
  #search .mintui.mintui-search {
    margin-right: 5px;
    color: #999;
  }

  /* searchbar inner padding fix */
  #search .mint-searchbar-inner {
    padding: 0 6px;
  }

  /* searchbar cancel button 颜色 */
  #search .mint-search .mint-searchbar-cancel {
    color: gray;
  }

  /* 取消 picnic.css 的样式冲突 */
  .mint-search input[type=search]:focus {
    border: none;
    outline: initial;
  }

</style>


