import Vue from 'vue'
import Resource from 'vue-resource'
import Router from 'vue-router'
import App from './App.vue'

import SearchView from './components/SearchView.vue'
import SearchResultsView from './components/search/SearchResultsView.vue'
import ItemView from './components/item/ItemView.vue'

// stylesheets
require("picnic/src/picnic.scss");
require("./assets/stylesheets/application.scss")
require('vue-animate/dist/vue-animate.min.css')
require('vue-msgbox/lib/vue-msgbox.css') // msgbox css

// import mint-ui cell
import Mint from 'mint-ui'
import 'mint-ui/lib/style.css'
Vue.use(Mint)

// async requests
Vue.use(Resource)

// router
Vue.use(Router)

var router = new Router()
router.map({
  '/': {
    component: SearchView
  },
  '/search/:id': {
    component: SearchResultsView
  },
  '/items/:id': {
    component: ItemView
  }
})

router.beforeEach(function () {
  window.scrollTo(0, 0)
})

router.redirect({
  '*': '/'
})

router.start(App, "#app")



