<template>
  <div id="search-results-list">
    <section class="search-results" v-if="results && (results.length > 0)">
      <search-result v-for="item in results" :item="item">
      </search-result>
    </section>

    <section v-else class="center island small mute">
      <p>没有相关产品</p>
    </section>
  </div>

</template>


<script>
import SearchResult from './SearchResult.vue'

export default {
  name: 'SearchResultsList',

  props: {
    results: Array
  },

  components: {
    SearchResult
  }
}
</script>

<style>

#search-results-list {
  background-color: white; padding-top: 8px; padding-bottom: 8px; 

  margin: 0 auto 20px;

  border-top: 1px solid #d9d9d9;
  border-bottom: 1px solid #d9d9d9;
}

.search-result + .search-result {
  border-top: 1px solid #cccccc;
  padding-top: 8px;
  margin-top: 16px;
}

</style>


