<template>
<div id="nav-wrapper">
  <nav class="white three navbar" style="">

    <div class="left-bar-button">
        <a v-link="{path: path, activeClass: 'hide-link'}">
          <img v-bind:src="backImageSrc" style="width: 30px;" />
        </a>
    </div>

    <div>
      <mt-search :value.sync="title"><a v-link="{path: path, activeClass: 'hide-link'}"></mt-search>      
    </div>

  </nav>
</div>

</template>

<script>

export default {
  name: 'SearchNavBar',
  props: {
    path: {
      type: String,
      default: "/"
    },
    title: {
      type: String,
      default: "搜索"
    }
  }, 
  data() {
    return {
      backImageSrc: require("../../assets/images/checkout_back_button.png")
    }
  }
}
</script>


<style>
/* native style clear nav */
nav.clear {
  background: none;
  box-shadow: none;
}

nav.navbar {
  height: 64px; padding-top: 20px;
}

nav.three {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.left-bar-button {
  padding-left: 16px;
}

.title {
  flex-grow: 1;
  text-align: center;
}

/* old purple branding */
nav.purple {
  background-color: #740092;
}

nav.white {
  background-color: white;
}

</style>


