<template>
  <div id="search-results-view">

    <!-- search bar -->
    <search-nav-bar></search-nav-bar>

    <!-- error cover screen -->
    <error-view v-show="showError"></error-view>

    <!-- loading cover -->
    <loading-cover v-show="isLoading" transition="fade">
    </loading-cover>

    <search-results-list :results="results"></search-results-list>
  </div>
</template>

<script>
import LoadingCover from '../shared/LoadingCover.vue'
import ErrorView from '../shared/ErrorView.vue'

import SearchNavBar from './SearchNavBar.vue'
import SearchResultsList from './SearchResultsList.vue'

export default {
  name: 'SearchResultsView',

  components: {
    ErrorView,
    LoadingCover,
    SearchNavBar,
    SearchResultsList
  },

  data() {
    return {
      query: "",
      results: [],
      isLoading: false,
      showError: false,
    }
  }, 

  ready() {
    this.getResults()
  },

  route: {
    data ({ to }) {

      var query = to.params.id
      this.$set('query', query)
      this.getResults()
    },

    deactivate (transition) {
      // reset state
      this.$set('isLoading', true)
      transition.next()
    }
  }, 

  methods: {

    getResults () {
      var vm = this;

      // check query is valid
      if (this.query.length < 1) {
        return 
      } 

      // request callback
      var callback = function(response) {
          console.log("[getResults callback] response: " + JSON.stringify(response.body));
          vm.$set('results', response.json());

          // remove loading cover
          vm.$set('isLoading', false)
      }

      var errorCallback = function(response) {
          console.log("[error callback] response: " + JSON.stringify(response));

          // remove loading cover
          vm.$set('isLoading', false)

          // show error
          vm.$set('showError', true)
      }

      // request url 
      var url = API_URL + "/api/items/";

      // search param
      var query = this.query
      var params = {query: query}

      // request headers
      var headers = {
          "Content-Type": "application/json",
          "Accept": "application/json",
          "X-APP-KEY": APP_KEY,
          "X-APP-SECRET": APP_SECRET
      };

      // vue-resource
      this.$http.get(url, {headers: headers, params: params}).then(callback, errorCallback);
    }, 

  }
}
</script>

